
  
  ul{
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    margin: 0;
    padding-left: 55%;
    padding-top: 40%;

  }
  
  ul li {
    list-style: none;
    margin: 0 5px;
    margin-left: 40px;
  }
  
  ul li a .fa {
    font-size: 40px;
    color: #262626;
    line-height: 80px;
    transition: 0.5s;
    padding-right: 14px;
  }
  
  ul li a span {
    padding: 0;
    margin: 0;
    position: absolute;
    top: 30px;
    color: rgb(81, 255, 0);
    letter-spacing: 4px;
    transition: 0.5s;
    font-size: 10px;
    
  }
  
  ul li a {
    text-decoration: none;
    display: absolute;
    display: block;
    width: 210px;
    height: 80px;
    background: rgb(0, 0, 0);
    text-align: left;
    padding-left: 20px;
    transform: rotate(-30deg) skew(25deg) translate(0, 0);
    transition: 0.5s;
    box-shadow: -20px 20px 10px rgba(0, 0, 0, 0.5);
  }
  
  ul li a:before {
    content: "";
    position: absolute;
    top: 10px;
    left: -20px;
    height: 100%;
    width: 20px;
    background: rgb(81, 255, 0);
    transform: rotate(0deg) skewY(-45deg);
  }
  
  ul li a:after {
    content: "";
    position: absolute;
    bottom: -20px;
    left: -10px;
    height: 20px;
    width: 100%;
    background: rgb(81, 255, 0);
    transform: rotate(0deg) skewX(-45deg);
  }
  
  ul li a:hover {
    transform: rotate(-30deg) skew(25deg) translate(20px, -15px);
    box-shadow: -50px 50px 50px rgba(0, 0, 0, 0.5);
  }
  
  ul li:hover .fa {
    color: black;
  }
  
  ul li:hover span {
    color: black;
  }
  
  ul li:hover:nth-child(1) a {
    background: rgb(81, 255, 0);
  }
  
  ul li:hover:nth-child(1) a:before {
    background: rgb(125, 255, 65);
  }
  
  ul li:hover:nth-child(1) a:after {
    background: rgb(139, 255, 85);
  }
  
  ul li:hover:nth-child(2) a {
    background: rgb(81, 255, 0);
  }
  
  ul li:hover:nth-child(2) a:before {
    background: rgb(139, 255, 85);
  }
  
  ul li:hover:nth-child(2) a:after {
    background: rgb(139, 255, 85);
  }
  
  ul li:hover:nth-child(3) a {
    background: rgb(81, 255, 0);
  }
  
  ul li:hover:nth-child(3) a:before {
    background: rgb(139, 255, 85);
  }
  
  ul li:hover:nth-child(3) a:after {
    background: rgb(139, 255, 85);
  }
  
  ul li:hover:nth-child(4) a {
    background: rgb(81, 255, 0);
  }
  
  ul li:hover:nth-child(4) a:before {
    background:rgb(139, 255, 85);
  }
  
  ul li:hover:nth-child(4) a:after {
    background: rgb(139, 255, 85);
  }
  